import React, { useState, useEffect } from "react";
import { TextField, Grid, IconButton, Button } from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import { useLanguage } from "../utilities/LanguageContext";
import { TEXT, BOTMESSAGE_BACKGROUND } from "../utilities/constants";
import { useTranscript } from "../utilities/TranscriptContext";

function ChatInput({ onSendMessage, processing }) {
  const [message, setMessage] = useState("");
  const [helperText, setHelperText] = useState("");
  const { language } = useLanguage();
  const { transcript, setTranscript, isListening } = useTranscript();

  useEffect(() => {
    if (!isListening && transcript) {
      setMessage(prevMessage => prevMessage ? `${prevMessage} ${transcript}` : transcript);
      setTranscript(""); // Clear the transcript buffer
    }
  }, [isListening, transcript, setTranscript]);

  const handleTyping = (event) => {
    if (helperText) {
      setHelperText("");
    }
    setMessage(event.target.value);
  };

  const handleSendMessage = () => {
    if (message.trim() !== "") {
      onSendMessage(message);
      setMessage("");
    } else {
      setHelperText(TEXT[language].HELPER_TEXT);
    }
  };

  const getMessage = (message, transcript, isListening) => {
    if (isListening) {
      if (transcript.length) {
        return message.length ? `${message} ${transcript}` : transcript;
      }
    }
    return message;
  };

  return (
    <div style={{
      display: 'flex',
      alignItems: 'center',
      backgroundColor: BOTMESSAGE_BACKGROUND, // Light blue background 1E50B4
      borderRadius: '20px',
      padding: '8px 16px',
      margin: '10px 0',
    }}>
      <TextField
        multiline
        maxRows={4}
        fullWidth
        disabled={isListening}
        placeholder={TEXT[language].CHAT_INPUT_PLACEHOLDER}
        id="USERCHATINPUT"
        value={getMessage(message, transcript, isListening)}
        onKeyDown={(e) => {
          if (e.key === "Enter" && !e.shiftKey && !processing) {
            e.preventDefault();
            handleSendMessage();
          }
        }}
        onChange={handleTyping}
        helperText={isListening ? TEXT[language].SPEECH_RECOGNITION_HELPER_TEXT : helperText}
        sx={{
          '& .MuiOutlinedInput-root': {
            '& fieldset': {
              border: 'none',
            },
          },
          '& .MuiInputBase-input': {
            padding: '8px 0',
          },
          '& .MuiInputBase-input::placeholder': {
            color: '#1E50B4', // Replace with the color you want
            opacity: 1, // This is to override the default opacity
          },
        }}
        InputProps={{
          disableUnderline: true,
          style: { color: '#1E50B4' },
        }}
        variant="standard"
      />
      <Button
        disabled={processing || isListening}
        onClick={handleSendMessage}
        sx={{
          minWidth: 'auto',
          padding: '8px',
          color: message.trim() !== "" ? '#1E50B4' : '#1976d2',
        }}
      >
        <SendIcon />
      </Button>
    </div>
  );
}

export default ChatInput;
